import React, {useEffect, useState} from 'react';
import {Link} from 'react-router-dom';
import axios from 'axios';
import '../../css/Pay_W.css';
// import data from '../../data/CartData';

function DirectPay_W({match}) {

    const [product, setProduct] = useState([]);
    const i = match.params.id;
    let P_Price = product[i].price;
    let P_DPrice = product[i].price_d;

    useEffect(() => {
        axios
        .get(`/api/w/direct_pay/${i}`)
        .then((result) => {setProduct(result.data.response)});
    }, []);

    return (
        <div className="PayPage">
            <nav className="P_Nav">
                <h4>CART</h4>
                <p>1.   {` > `}  2.   {` > `} 3. </p>
            </nav>

            <content className="P_Main">
            {/*    */}
                <Cart product={product} />

                <div className="P_Main2">
                    <pre></pre>
                    <pre></pre>
                    <pre></pre>
                    {/*   */}
                    <val>{P_Price} </val>
                    <val>+</val>
                    {/*  */}
                    <val>{P_DPrice} </val>
                    <val>=</val>
                    {/*  */}
                    <val> {Number(P_Price) + Number(P_DPrice)} </val>
                </div>
                <div className="P_Main3">
                    <Link to="/w/payment/:id" className="Pay_Button"> <p>  </p> </Link>
                </div>
            </content>

        </div>
    )
}



function Cart(props) {
    return (
            <div className="C_Main1">
                <div className="C_Author">
                    {props.product.authorName} 
                </div>
                <div className="C_Product">
                    {/* {props.product.img}<br /> */}
                    {props.product.productName}<br />
                      /  / {props.product.productDetailPrice} <br />
                    
                    <div className="C_Choose">
                    <button onClick={ <Remove product={props.product} key={props.i} />}>  </button>
                </div>
                </div>
                <div className="C_Price">
                    <h4>   </h4>
                    <h5> {props.product.productDetailPrice}  </h5>
                </div>
                <div className="C_DPrice">
                    <h4>  </h4>
                    <h5> 3000  </h5>
                </div>
            </div>
    )
}

function Remove() {
    return (
        <div>

        </div>
    )
}

export default DirectPay_W;