import React, {useEffect, useState} from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
//import data from '../../data/WorkData';
// import data from '../../data/WorkDetailData';  //   ()
import '../../css/WorkDetailPage.css';
import IMG1 from '../../img/DetailIMG1.jpg';
import { ColorLensOutlined } from '@material-ui/icons';
import starImg from '../../img/stars.png';


//   ------------------------------

function WorkDetailPage({match}) {
    const i = match.params.id; // (work/detail/:id)  id   
    const [product, setProduct] = useState([]);
    const [tab, setTab] = useState(1);

    useEffect(() => {
        axios
          .get(`/api/w/product/${i}`)
        //   .then((result) => console.log(result.data.response));
          .then((result) => setProduct(result.data.response));
      }, []);
      

    return (
        <div className="WorkDetail">
                {/*    */}
                <div className="Content1">
                    <div className="Img_big"><img src={IMG1} /></div>    
                    <div className="Img_small"><img src={IMG1} /></div>
                    <div className="Img_small"><img src={IMG1} /></div>
                    <div className="Img_small"><img src={IMG1} /></div>
                    <div className="Img_small"><img src={IMG1} /></div>
                    <div className="menu">
                        <div className="menubar_W">
                            <button onClick={()=>{setTab(1)}}></button>
                            <button onClick={()=>{setTab(2)}}> /  / </button>
                            <button onClick={()=>{setTab(3)}}></button> 
                            <button onClick={()=>{setTab(4)}}></button>
                        </div>
                        <TabChanged tab = {tab} product={product} />
                    </div>
                </div>

                {/*    */}
                <div className="Content2">
                        {/*  /  /  /  /  /  /  /  /  */}

                        <div className="made_by">{product.authorName} {'>'}</div>
                        <div className="category_right">{product.categoryName}</div>
                        <div className="title">{product.name}</div>
                        {/* <div className="discount_per">{productt[0].discount_percent}</div> */}
                        <div className="discount_price">{product.price}</div>
                        {/* <div className="price">{productt[0].price}</div> */}
                        <div className="reserves"><p>&nbsp;&nbsp;&nbsp;</p> <p>{(product.price)*0.05} p</p></div>
                        <div className="review_count"><p></p> <p>{(product.rate)*0.1} ({product.review})</p></div>
                        <div className="shipping_fee"><p>&nbsp;&nbsp;&nbsp;</p> <p>3000 </p></div>
                        {/* <div className="product_quantity"><p>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</p> <p>{product[i].product_quantity} </p></div> */}
                        <div className="total_amount"><p> </p><span>{product.price}</span></div>
                        <div className="cart_button"><Link to={`/w/cart/${i}`}> <p></p> </Link></div>
                        {/* <button className="cart_button" onClick={()=>{
                            axios.post('   ', {id:}, {options:}, {})
                            }}>  </button> ----------     axios  */}
                        <div className="pay_button"><Link to={`/w/directPay`}> <p></p> </Link></div>
                        
                </div>
        </div>
    );
}

export default WorkDetailPage;

function TabChanged (props) {
    if(props.tab === 1) {
        return (
            <div className="menu_1">
                <h5></h5>
                <p>
                      1 !!!<br /> 
                     "" ""  <br />

                     3mm        :)<br />
                     1    .<br />
                    ( /      )<br />
                </p>
            </div>
        )
    }
    else if(props.tab === 2){
        return (
            <div className="menu_2">
                <h5> /  / </h5>
                <p>
                <div className="delivery1">
                <h3>
                    
                </h3>
                <h4>
                     : 3000 <br/>
                </h4>
                </div>
                <div className="delivery2">
                <h3> / </h3>
                <h4>7  </h4>
                </div>
                <div className="delivery3">
                <h3> / </h3>
                <h4></h4>
                </div>
                </p>
            </div>
        )
    }
    // 
    else if(props.tab === 3){
        return (
        <div className="menu_3">
            <h5> ({})</h5>
            <p>
                {
                    props.product.reviewDTOList.map((review, i) => {
                        const star = (review.rate)*2 ;
                        return (
                            <div>
                                <h1>{review.userName}</h1>
                                {/* <h2>{rate}</h2> */}
                                <h2><img src={starImg} /></h2>
                                <h3>{review.date}</h3>
                                <h4>{review.content}</h4>
                            </div>
                        );
                    })
                }
            </p>
            <div>
            </div>
        </div>
        )
    }
    //  
    else if(props.tab === 4){
        return (
        <div className="menu_4">
            <h5></h5>
            <p>
                {
                    props.product.commentList.map((comment, i) => {
                        return (
                            <div>
                                <h1> {comment.userName}</h1>
                                <h2>{comment.content}</h2>
                            </div>
                        );
                    })
                }
            </p>
        </div>
        )
    }
}





